.container {
  flex: 1;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  padding: 5%;
  display: flex;
  flex-direction: column;
}

.icons {
  flex: 4;
  flex-direction: row;
  align-items: center;
  display: flex;
}

.icon {
  margin: 15px;
}

.controls {
  flex: 1;
  align-items: center;
}

.insertPrivate {
  margin: 20px;
}

.rowControls {
  flex: 1;
  flex-direction: row;
  justify-content: space-evenly;
  display: flex;
  width: 100%;
  margin: 15px 0;
}

.private {
  font-weight: 600;
}

.inputWrapper {
  background-color: #F3F3F3;
  border-bottom: 2px solid #6894BC;
  padding: 5px;
}

.input {
  width: 100%;
  color: #4F4F4F;
  font-weight: 500;
  font-size: 16px;
  outline: none;
  height: 20px;
  border: none;
  background-color: #F3F3F3;
}

.label {
  color: #828282;
  font-size: 12px
}

.privateTextGen {
  color: #4F4F4F;
  font-weight: 500;
  font-size: 16px;
  margin: 15px 0;
}

.publicPoints {
  color: #FFF;
  font-size: 16px;
  padding-left: 20px;
  padding-right: 20px;
}
