@font-face {
  font-family: 'Avenir';
  src:url('../../assets/Avenir.ttf');
}

html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  min-height: 310px;
  font-family: 'Avenir';
}

.app {
  flex-direction: column;
  height: 100%;
  display: flex;
  background-color: #FFF;
}

.logo {
  display: flex;
}

.icon {
  margin: 0 5px;
}

.header {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1 1;
  background-color: #6894BC;
}

.content {
  display: flex;
  flex: 6 6;
  background-color: #FFF;
  justify-content: center;
  align-items: center;
}

.headerText {
  font-family: 'Avenir';
  font-size: 24px;
  color: #FFFFFF;
}