.tableRow {
  display: flex;
  border-top: 0.5px solid #E8E8E8;
  padding: 7px;
}

.tableRow:last-child {
  border-bottom: 0.5px solid #E8E8E8;
}

.tableCell {
  width: 200px;
  color: #4F4F4F;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding: 0 10px;
}

.generateCheckboxContainer {
  margin: 15px;
}

.message {
  font-weight: 500;
  font-size: 16px;

  color: #4F4F4F;
}